import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import { Flex, Card } from 'rebass'
import styles from '../styles/writeups.module.css'

class Writeup {
  static PROBLEM_TYPE = Object.freeze({
    REV: 'rev',
    PWN: 'pwn',
    CRYPTO: 'crypto',
    WEB: 'web',
    FORENSIC: 'forensic',
    STEGO: 'stego',
    PROG: 'prog',
    RECON: 'recon',
    MISC: 'misc',
  })

  constructor(path) {
    this.path = path
    let _path = path.split('/')
    this.ctf_name = _path[2]
    this.problem_type = this.getProblemType(_path[3])
    this.problem_name = _path[4]
    this.author = _path[5]
  }

  getProblemType(problem_type) {
    switch (problem_type.toLowerCase()) {
      case 're':
      case 'rev':
      case 'reverse':
      case 'reversing':
        return Writeup.PROBLEM_TYPE.REV
      
      case 'pwn':
      case 'exploit':
      case 'exploiting':
        return Writeup.PROBLEM_TYPE.PWN
      
      case 'crypto':
        return Writeup.PROBLEM_TYPE.CRYPTO
      
      case 'web':
        return Writeup.PROBLEM_TYPE.WEB
      
      case 'forensic':
      case 'forensics':
        return Writeup.PROBLEM_TYPE.FORENSIC
      
      case 'stego':
        return Writeup.PROBLEM_TYPE.STEGO

      case 'prog':
      case 'code':
      case 'coding':
        return Writeup.PROBLEM_TYPE.PROG

      case 'recon':
        return Writeup.PROBLEM_TYPE.RECON

      case 'misc':
      default:
        return Writeup.PROBLEM_TYPE.MISC
    }
  }

  getBG() {
    switch (this.problem_type.toLowerCase()) {
      case Writeup.PROBLEM_TYPE.REV:
        return '#784c50'
      
      case Writeup.PROBLEM_TYPE.PWN:
        return '#336085'
      
      case Writeup.PROBLEM_TYPE.CRYPTO:
        return '#a09950'
      
      case Writeup.PROBLEM_TYPE.WEB:
        return '#6090b0'
      
      case Writeup.PROBLEM_TYPE.FORENSIC:
        return '#309070'
      
      case Writeup.PROBLEM_TYPE.STEGO:
        return '#70a050'

      case Writeup.PROBLEM_TYPE.PROG:
        return '#7b6699'

      case Writeup.PROBLEM_TYPE.RECON:
        return '#aa7050'

      case Writeup.PROBLEM_TYPE.MISC:
      default:
        return '#757575'
    }
  }
}

export default ({ data, location }) => {
  const writeups = data.allSitePage.edges
    .filter(({node}) => (node.path.match(/\//g) || []).length === 5)
    .map(({node}) => new Writeup(node.path))
  const non_writeups = data.allSitePage.edges.filter(({node}) => (node.path.match(/\//g) || []).length !== 5)
  console.log(writeups)
  console.log(writeups.length)
  console.log(non_writeups)
  console.log(non_writeups.length)
  return (
    <Layout location={location}>
      <h2>Writeups</h2>
      <Flex flexWrap='wrap' mx={-2}>
        {writeups.map((writeup, idx) =>
          <a href={writeup.path} style={{
            color: 'inherit',
            textDecoration: 'none',
          }}>
            <Card
              className={styles.card}
              style={{
                background: writeup.getBG(),
              }}
              p={3}
              m={3}
              bg='#ffffff'
              borderRadius={4}
              boxShadow='0 0 4px rgba(0, 0, 0, 0.16)'
            >
              <p>{writeup.problem_name}</p>
              <p>{writeup.problem_type}</p>
              <p>{writeup.ctf_name}</p>
              <p>{writeup.author}</p>
            </Card>
          </a>
        )}
      </Flex>
    </Layout>
  )
}

export const query = graphql`
  query {
    allSitePage(
      filter: {
        path: {
          regex: "/.*\/writeups\/.+$/i"
        }
      },
    ) {
      edges {
        node {
          id
          path
        }
      }
    }
  }
`